<template>
    <div class="container">
      <el-row style="margin-bottom: 0px">
        <el-col :span="3">
          <el-button type="success" @click="yjfp"></el-button>

        </el-col>

        <el-col :span="21">
          <el-switch
            style="display: block"
            v-model="sexboolean"
            active-color="#13ce66"
            inactive-color="#ff4949"
            active-text=""
            inactive-text=""
            @change="sexChang">
          </el-switch>
        </el-col>
      </el-row>


      <div class="divbuju">
        <div style="border-bottom:2px solid #ddd;">
          <el-row style="margin-bottom: 0px">

              <el-col :span="4" style="padding-top: 10px"></el-col>
              <el-col :span="12">
                <el-form :inline="true" :model="dormitory">
                  <el-form-item >
                    
                    <el-select v-model="dormitory.dong" placeholder="" @change="dongChange"  size="mini">
                      <el-option v-for="item in donglist" :key="item" :label="item"
                                 :value="item"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item >
                    
                    <el-select v-model="dormitory.floor" placeholder="" @change="floorChange"  size="mini">
                      <el-option v-for="item in floorlist" :key="item" :label="item"
                                 :value="item"></el-option>
                    </el-select>

                  </el-form-item>
                </el-form>
              </el-col>
              <el-col :span="8" style="padding-top: 10px">
                {{mcname}}
              </el-col>

          </el-row>
        </div>

        <el-row>
          <el-col :span="6">
            <div class="divbuju" style="height: 580px; width: 250px;overflow-y: auto" >
                <div style="border-bottom:2px solid #ddd; height: 30px">
                      {{this.sexboolean? '':''}}
                </div>
                <div v-for="cls in classlist">
                  <el-button style="width: 250px" @click="clickclass(cls)">{{cls.c.m.name}}{{cls.c.name}}{{cls.lessnumber}}</el-button>
                </div>
            </div>
          </el-col>
          <el-col :span="18">
            <div class="divbuju" style="width: 900px; height: 580px; padding: auto">
              <div  v-for="dor in dormitorylist" @mouseover="mouseoverdiv(dor)" @mouseleave="mouseleavediv(dor)" style="height: 50px;width: 100px;float: left;margin: 10px;" >
                <el-row style="margin-bottom: 0px">
                  <el-col :span="20">
                    <div class="jichudiv" @click="clickdormitory(dor)" :class="{'jichudivred':dor.hasnumber==5,'jichudivgreen':dor.hasnumber>0&&dor.hasnumber<5}">
                      {{dor.roomname}}<br/>
                      {{dor.hasnumber}} / {{dor.number}}
                    </div>
                  </el-col>
                  <el-col :span="4">
                    <div v-show="dor.show">
                    <el-row style="margin-bottom: 0px">
                      <el-col>
                        <div style="width: 20px;height: 20px;background-color: #ff0705">
                          <i class="el-icon-refresh-left" @click="refreshdormitory(dor)"></i>
                        </div>
                      </el-col>
                    </el-row>
                    <el-row style="margin-bottom: 0px">
                      <el-col>
                        <div style="width: 20px;height: 20px;background-color: #4bddb1;margin-top: 10px">
                          <i class="el-icon-search" @click="getstudent(dor)"></i>
                        </div>
                      </el-col>
                    </el-row>
                    </div>
                  </el-col>
                </el-row>

              </div>
            </div>
          </el-col>
        </el-row>

      </div>

      <el-row style="margin-bottom: 0px">
        <el-col :span="4" :offset="12">
          <div class="jichudivred" style="width: 80px;height: 50px; float: left"></div> 
        </el-col>
          <el-col :span="4" >
          <div class="jichudivgreen" style="width: 80px;height: 50px ;float: left"></div> 
          </el-col>
            <el-col :span="4" >
          <div style="width: 80px;height: 50px;background-color: #b6f3ff;float: left" ></div>
        </el-col>
      </el-row>


      <!--  -->
      <el-dialog title="" :visible.sync="dormitoryShowInfoVisible" :close-on-click-modal="false"
                 >
        <div class="divbuju">


          <div class="divbuju">
            <el-row>
              <el-col :span="6"><h1></h1></el-col>
              <el-col :span="18"> <h1>{{dormitoryClass}}</h1></el-col>
            </el-row>
          </div>


          <div class="divbuju">
            <h1></h1>
            <template>
              <!---->
              <el-table :data="dormitoryStudent"
                        size="mini"
                        highlight-current-row border
                        class="el-tb-edit mgt20"
                        ref="multipleTable"
                        tooltip-effect="dark"
              >
                <!---->
                <el-table-column type="index" :index="indexMethod">
                </el-table-column>
                <el-table-column label="" prop="c.m.name">
                </el-table-column>
                <el-table-column label="" prop="c.name">
                </el-table-column>
                <el-table-column label="" prop="sb.name">
                </el-table-column>
                <el-table-column label="" prop="studentId">
                </el-table-column>
                <el-table-column label="" prop="sb.sex">
                </el-table-column>
                <el-table-column label="" prop="sb.phone">
                </el-table-column>
                <el-table-column label="" prop="sb.email">
                </el-table-column>

              </el-table>
            </template>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dormitoryShowInfoVisible=false"></el-button>
        </div>
      </el-dialog>



    </div>
</template>

<script>
    export default {
        name: "DorDivideDormitory",
      created() {

      },
      mounted() {
      this.initdonglistandclasslist();
      },
      methods: {

          //
        sexChang(){
          this.dormitory.dong="";
          this.dormitory.floor="";
          this.initdonglistandclasslist();
        },

        //
        initdonglistandclasslist(){
          let sex=this.sexboolean? '':'';
          //
          this.postRequest('/dormitory/alldormitory/getIinsufficientClassDormitoryBySex', {
            sex: sex
          }).then(resp => {
            this.classlist=resp.data;
          });

          //
          this.postRequest('/dormitory/alldormitory/getDormitoryDong', {
            sex: sex
          }).then(resp => {
            this.donglist=resp.data;
            if( this.dormitory.dong==""){
              this.dormitory.dong=this.donglist[0];
            }
            this.dongChange();
          });

        },


        //
        dongChange(){
          this.postRequest('/dormitory/alldormitory/getDormitoryFloor', {
            dong: this.dormitory.dong
          }).then(resp => {
            this.floorlist=resp.data;
            if(this.dormitory.floor==""){
              this.dormitory.floor=this.floorlist[0];
            }
            this.floorChange();
          });
        },

        //
        floorChange(){
          // alert(""+this.dormitory.dong+""+this.dormitory.floor+"");
          this.postRequest('/dormitory/alldormitory/getallDormitory', {
            dong: this.dormitory.dong,
            floor:this.dormitory.floor
          }).then(resp => {
            this.dormitorylist=resp.data;
            console.info(JSON.stringify(this.dormitorylist));
          });

        },
        //
        clickclass(cls){
          this.mcname=cls.c.m.name+""+cls.c.name;
          this.classId=cls.c.classId;
          this.lessnumber=cls.lessnumber;
        },

        //
        clickdormitory(dor){
          if(this.lessnumber!=""){
              //
              let surplusnumber=dor.number-dor.hasnumber;
              if(surplusnumber>0){
                //
                let mynumber=0;
                //
                if(this.lessnumber>surplusnumber){
                  mynumber=surplusnumber;
                  this.lessnumber=this.lessnumber-surplusnumber;
                }else{  //
                  mynumber=this.lessnumber;
                  this.mcname="";
                  this.lessnumber="";
                }
                //
                this.postRequest('/dormitory/alldormitory/divideDormitory', {
                  classId: this.classId,
                  dormitoryId:dor.id,
                  number: mynumber
                }).then(resp => {
                  this.initdonglistandclasslist();
                });

              } else{
                this.$message({
                  message:dor.roomname+"",
                  type:"warning"
                });
              }
          }else{
            this.$message({
              message:"",
              type:"warning"
            });
          }

        },
        //
        yjfp(){
          this.$confirm("?()", "", {
            type: "warning"
          }).then(() => {
            this.postRequest('/dormitory/alldormitory/dividedormitory', {}).then(resp => {
              if (resp && resp.status == 200) {
                this.initdonglistandclasslist();
              }
            });
          })
            .catch(() => {
            });
        },

        //
        mouseoverdiv(dor){
          dor.show=true;
        },
        //
        mouseleavediv(dor){
          dor.show=false;
        },

        //
        getstudent(dor) {
          this.dormitoryShowInfoVisible = true;
          var _this = this;
          this.postRequest('/dormitory/alldormitory/getStudentByDormitoryId', {
            dormitoryId: dor.id
          }).then(resp => {
            if (resp && resp.status == 200) {
              console.info(JSON.stringify(resp.data))
              let cs = resp.data[0];
              let classname = "";
              if (cs.length > 0) {
                for (let i = 0; i < cs.length; i++) {
                  classname += cs[i].m.name + cs[i].name + ""
                }
              }
              classname = classname.substring(0, classname.length - 1);
              this.dormitoryClass = classname;
              this.dormitoryStudent = resp.data[1];
              console.info(JSON.stringify(resp.data[0]) + JSON.stringify(resp.data[1]));

            }
          });
        },

        //
        refreshdormitory(dor){
          this.$confirm("?", "", {
            type: "warning"
          }).then(() => {
            this.postRequest('dormitory/dividedormitory/deletelistDormitoryAllocation', {
              id: dor.id
            }).then(resp => {
              if (resp && resp.status == 200) {
                this.initdonglistandclasslist();
              }
            });
          })
            .catch(() => {
            });
        },
      },
      data(){
        return {

          // false   /  true
          sexboolean:true,
          //
          dormitory:{
            dong:"",
            floor:"",
            sex:"",
            number:"",
          },

          //
          donglist:[],

        //
          floorlist:[],

          //
          dormitorylist:[],

          //
          classlist:[],

          //,,
          mcname:"",
          classId:"",
          lessnumber:"",


          //
          dormitoryStudent: [],
          dormitoryClass: "",
          dormitoryShowInfoVisible: false,

        }
      }
    }
</script>

<style scoped>
  /*//*/
  .jichudiv{
    width: 80px;
    height: 50px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
    float: left;
    margin: 10px;
    background-color: #b6f3ff;
  }
  .jichudivred{
    background-color: #ff518a;
  }
  .jichudivgreen{
    background-color: #8bff56;
  }
</style>



